import React from 'react'
import { connect } from 'react-redux'
import { fetchMenu, deleteMenuItem } from '../../../Store/actions/Menu'
//import MenuCard from '../../../Components/MenuCard'
import VerticalNav from './VerticalNav'
import Loading from '../../../Components/Loading'
import Fab from '../../../Components/Fab'
import { fetchCategory } from '../../../Store/actions/Category'
import Grid from '@material-ui/core/Grid'
import { makeStyles } from '@material-ui/core/styles'
import { useHistory } from 'react-router-dom'

const useStyles = makeStyles(theme => ({
  menuCardContainer: {
    padding: theme.spacing(1)
  }
}))

function makeGroup(menu, category){
  let processed = {}
  if(category.length === 0 || menu.length === 0){
    return []
  }
  for(let i = 0; i < category.length; i++){
    processed[category[i].id] = ({heading: category[i].label, items: []})
  } 
  //console.log(processed)
  for(let i = 0; i< menu.length; i++){
    if(processed[menu[i].category_id] === undefined){
      continue;
    }
    processed[menu[i].category_id].items.push(menu[i])
  }
  let _processed = []
  for(let item in processed){
    _processed.push(
      processed[item]
    )
  }
  //console.log(_processed)
  return _processed
}

function Menu(props){
  const classes = useStyles()
  let history = useHistory()
  React.useEffect(() => {
    if(props.Menu.items.length === 0){
      props.fetchMenu()
    }
    if(props.Category.items.length === 0) {
      props.fetchCategory()
    }
  }, [])

  function editDish(id) {
    history.push('/dish/edit/'+id)
  }

  function deleteDish(id){
    if(window.confirm("Do you wish to delete?")){
      props.deleteMenuItem(id)
    }
  }

  function viewDish(id){
    history.push('/dish/view/'+id)
  }

  function fabClick(){
    history.push('/dish/add')
  }
  
  let group = makeGroup(props.Menu.items, props.Category.items)

  return (
    <Grid container>
      <Loading isLoading={props.Menu.isFetching}>
        {/* {
          props.Menu.items.map(dish => 
            <Grid item xs={12} sm={6} key={dish.id} className={classes.menuCardContainer}>
              <MenuCard image={dish.image}
                description={dish.description}
                name={dish.label}
                id={dish.id}
                editDish={editDish}
                deleteDish={deleteDish}
                viewDish={viewDish} />
            </Grid>
          )
        } */}
        <VerticalNav 
          data={group}
          editClick={editDish}
          deleteClick={deleteDish}
          renderImages={true}
          flipRender={props.flipRenderImages}
          viewDish={viewDish}
          refreshMenu={() => props.fetchMenu()}
        />
      </Loading>
      <Fab onClick={fabClick} />
    </Grid>
  )
}

const mapStateToProps = state => ({
  Menu: state.Menu,
  Category: state.Category
})

const mapDispatchToProps = dispatch => ({
  fetchMenu: () => dispatch(fetchMenu(dispatch)),
  deleteMenuItem: id => dispatch(deleteMenuItem(dispatch, id)),
  fetchCategory: () => dispatch(fetchCategory(dispatch))
})

export default connect(mapStateToProps, mapDispatchToProps)(Menu)