import { initialState } from './index'

const CategoryReducer = (state = initialState.Category, action) => {
  //console.log(action.type, action.type === "CATEGORY::SUCCESS")
  switch(action.type){
    case "CATEGORY::REQUEST":
      return {
        ...state,
        isFetching: true
      }
    case "CATEGORY::SUCCESS":
      return {
        ...state,
        isFetching: false,
        items: action.data
      }
    case "CATEGORY::FAIL":
      return {
        ...state,
        isFetching: false
      }
    
    case "CATEGORY::ADD_REQUEST":
      return {
        ...state,
        addRequest: true
      }
    case "CATEGORY::ADD_SUCCESS":
      return {
        ...state,
        addRequest: false,
        items: [
          ...state.items,
          action.data
        ]
      }
    
    case "CATEGORY::ADD_FAIL":
      return {
        ...state,
        addRequest: false
      }
    
    case "CATEGORY::DELETE_REQUEST":
      return {
        ...state,
        deleteRequest: true
      }
    
    case "CATEGORY::DELETE_SUCCESS":
      return {
        ...state,
        items: state.items.filter(item => item.id !== action.id),
        deleteRequest: false
      }
      
    case "CATEGORY::DELETE_FAIL":
      return {
        ...state,
        deleteRequest: false
      }
    default:
      return state
  }
}

export default CategoryReducer