import API from '../../Services/API'
import { snackbarError, snackbarSuccess } from './snackbar'
import errorutil from '../../Services/ErrorUtil'
import { updateMenu } from './Menu'

const PAGE = "DISH::"

export const addDish = (dispatch, data) => {

  API('/api/dish', data, '', 'POST')
    .then(res =>{
      console.log(res.data)
      if(res.data.success){
        dispatch(snackbarSuccess("Dish added succesfully"))
        return dispatch({
          type: PAGE+"ADD_SUCCESS",
          item: res.data.data.item
        })

      }
      throw new Error("Unable to add Dish")
    })
    .catch(err => {
      console.log(err.response)
      dispatch(snackbarError(errorutil(err)))
      dispatch({
        type: PAGE+"ADD_FAIL"
      })
    })
  return ({
    type: PAGE+"ADD_REQUEST"
  })
}

export const getDish = (dispatch, id) => {
  API('/api/dish/'+id, '', '', 'GET')
    .then(res => {
      if(res.data.success){
        return dispatch({
          type: PAGE+"GET_SUCCESS",
          data: res.data.data[0]
        })
      }
      throw new Error("Unable to get dish")
    })
    .catch(err => {
      dispatch(snackbarError("Unable to fetch dish"))
    })
  return ({
    type: PAGE+"GET_REQUEST"
  })
}

export const updateDish = (dispatch, data, id) => {
  API('/api/dish/'+id, data, '', 'POST')
    .then(res => {
      if(res.data.success){
        dispatch(snackbarSuccess("Updated dish"))
        dispatch(updateMenu(res.data.data.item))
        return dispatch({
          type: PAGE+"UPDATE_SUCCESS",
          //data: res.data.data
        })
      }
      throw new Error("Unable to get dish")
    })
    .catch(err => {
      console.log(err.response)
      dispatch(snackbarError("Unable to update dish"))
    })
  return ({
    type: PAGE+"UPDATE_REQUEST"
  })
}