import React from 'react'
import Add from '../Add'
import { connect } from 'react-redux'
import { deserialize } from '../FormBuilder'
import { useParams } from 'react-router-dom'
import { updateDish, getDish } from '../../../Store/actions/Dish'

function EditDish(props){
  let {dishId} = useParams()
  //let [dishDefault, setDishDefault] = React.useState({})
  React.useEffect(() => {
    //if(props.Dish.item === undefined){
      props.getDish(dishId)
    
  }, [])
  let data = deserialize(props.Dish.item)
  function updateDish(data){
    //e.preventDefault()
    console.log(data)
    props.updateDish(data, props.Dish.item.id)
  }

  //let obj = deserialize(props.Dish.item)
  //console.log({...obj})
    return (
      <Add 
        loading={props.Dish.getRequest || props.Dish.updateRequest}
        selected={10}
        {...data}
        updateDish={updateDish}
        />
    )

}

const _mapStateToProps = state => ({
  Dish: state.Dish
})


const _mapDispatchToProps = dispatch => ({
  updateDish: (data, id) => dispatch(updateDish(dispatch, data, id)),
  getDish: id => dispatch(getDish(dispatch, id))
})


export default connect(_mapStateToProps, _mapDispatchToProps)(EditDish);