import React from 'react'
import TextField from '@material-ui/core/TextField'
import Edit from '@material-ui/icons/Edit'
import Delete from '@material-ui/icons/Delete'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ButtonBase from '@material-ui/core/ButtonBase'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import { deleteMenuItem } from '../../Store/actions/Menu'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import { connect } from 'react-redux'
import IconButton from '@material-ui/core/IconButton'
import { makeStyles } from '@material-ui/core/styles'
import { useHistory } from 'react-router-dom'
import fetchAPI from '../../Services/API'
import Divider from '@material-ui/core/Divider'

const styles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    padding: theme.spacing(1)
  },
  inputContainer: {
    display: 'flex',
    flex: 1
  },
  resultContainer: {
    padding: theme.spacing(1)
  },
  resultList: {
    background: '#fff',
    borderRadius: 4,
    position: 'relative',
    overflow: 'hidden',
    boxShadow: theme.shadows[1]
  },
  textClass: {
    textAlign: 'left',
    justifyContent: 'flex-start',
    width: '100%',
    height: '100%',
  },
  listItem: {
    alignItems: 'stretch'
  }
}))

function SearchBar(props){
  const classes = styles();

  return (
    <div className={classes.root}>
      <div className={classes.inputContainer}>
        <TextField placeholder="Search Dish" value={props.search} onChange={props.onChange}  fullWidth autoFocus/>
      </div>
    </div>
  )
}

const Item = props => <ListItem className={props.itemClass}>
  <ListItemIcon>
    <IconButton onClick={() => props.editClick(props.id)}>
      <Edit /> 
    </IconButton>
  </ListItemIcon>
  <ListItemText primary={
    <ButtonBase onClick={() => props.itemClick(props.id)} className={props.textClass}>{props.label}</ButtonBase>
    } />
  <ListItemSecondaryAction>
    <IconButton onClick={() => props.deleteClick(props.id)}>
      <Delete />
    </IconButton>
  </ListItemSecondaryAction>
</ListItem>


function SearchResult({ items, ...props }){
  const classes = styles();

  return <div className={classes.resultContainer}>
    <List className={classes.resultList}>
      {
        items.length > 0 ? items.map((item, index) => 
          <React.Fragment key={item.id}>
            <Item {...props} {...item} key={item.id} textClass={classes.textClass} itemClass={classes.listItem}/>
            {index + 1 === items.length ? null : <Divider />}
          </React.Fragment>
        ):
        <ListItem>
          <ListItemText primary={'Search Dishes to get suggestions'} />
        </ListItem>
      }
    </List>
  </div>
}

function _Search(props){

  const history = useHistory()
  let [input, setInput] = React.useState('')
  let [result, setResult] = React.useState([])

  function handleChange(e){
    setInput(e.target.value)
    if(e.target.value.length === 0){
      setResult([])
      return 
    }
    fetchAPI('/api/dish/search/'+e.target.value, '', '', 'GET')
      .then(res => {
        //console.log(res.data);
        setResult(res.data.data.dishes)
      })
      .catch(err => console.log(err.response))
  }

  function editDish(id) {
    history.push('/dish/edit/'+id)
  }

  function deleteDish(id){
    if(window.confirm("Do you wish to delete?")){
      props.deleteMenuItem(id)
    }
  }

  function viewDish(id){
    history.push('/dish/view/'+id)
  }

  return <div>
    <SearchBar onChange={handleChange} search={input} />
    <SearchResult itemClick={viewDish} deleteClick={deleteDish} editClick={editDish}  items={result}/>
  </div>
}


const mapDispatchToProps = dispatch => ({
  deleteMenuItem: id => dispatch(deleteMenuItem(dispatch, id)),
})

export default connect(null, mapDispatchToProps)(_Search)

