import React from 'react'
import {CategoryComponent} from '../Category'
import API from '../../Services/API'
import { snackbarError } from '../../Store/actions/snackbar'
import { connect } from 'react-redux'

function PaymentMethod(props){
  let [payment, setPayment] = React.useState({request: true, items: [] })
  let [addRequest, setAddRequest] = React.useState(false)
  React.useState(() => {
    API('/api/payment', '', '', 'GET')
      .then(res => {
        if(res.data.success){
          setPayment({
            request: false,
            items: res.data.data.message
          })
        }
      })
      .catch(err => console.log(err.response))
  }, [])

  const deletePayment = (id) => {
    if(!window.confirm("Do you wish to remove?")){
      return
    }
    API('/api/payment/'+id, '', '', 'DELETE')
      .then(res => {
        if(res.data.success){
          setPayment({
            request: false,
            items: payment.items.filter(item => item.id !== id)
          })
          return
        }
        throw new Error("Error occured")
      })
      .catch(err => {
        props.snackbarError("Unable to remove item")
        console.log(err.response)
      })
  }


  const addItem = (label) => {
    setAddRequest(true)
    API('/api/payment', {label}, '', 'POST')  
      .then(res => {
        console.log(res.data, res.data.success)
        if(res.data.success){
          setPayment({
            request: false,
            items: [...payment.items,  res.data.data.message]
          })
        }
        setAddRequest(false)
      })
      .catch(err => {
        props.snackbarError("Unable to add Category");
        console.log(err.response)
      })
  }
  return (
    <CategoryComponent 
      addRequest={addRequest}
      itemsRequest={payment.request}
      items={payment.items}
      heading={"Payment methods"} 
      addItem={addItem}
      actionClick={deletePayment}
    />
  )
}

const mapDispatchToProps = dispatch => ({
  snackbarError: msg => dispatch(snackbarError(msg))
})

export default connect(null, mapDispatchToProps)(PaymentMethod);