import { initialState } from './index'

const SettingsReducer = (state = initialState.Settings, action) => {
  //console.log(action.type, action.type === "CATEGORY::SUCCESS")
  switch(action.type){
    case "SETTINGS::TOGGLE_REFRESH_MENU":
      return {
        ...state,
        autoRefreshMenu: !state.autoRefreshMenu
      }

    case "SETTINGS::TOGGLE_REFRESH_CATEGORY":
      return {
        ...state,
        autoRefreshCategory: !state.autoRefreshCategory
      }

    case "SETTINGS::TOGGLE_REFRESH_PAYMENT":
      return {
        ...state,
        autoRefreshPayment: !state.autoRefreshPayment        
      }
    default:
      return state
  }
}

export default SettingsReducer