import React from 'react'
import AddDish from './Add'
import Menu from './Menu'
import Edit from './Edit'
import View from './View'
import { Route, Switch } from 'react-router-dom'

function Dish(props){
  return (
    <Switch> 
      <Route path={'/dish/add'} component={AddDish} />
      <Route path={'/dish/edit/:dishId'} component={Edit} />
      <Route path={'/dish/view/:dishId'} component={View} />
      <Route path={'/'} component={Menu} />
    </Switch>
  )
}

export default Dish;