import API from '../../Services/API'
import { snackbarError, snackbarSuccess } from './snackbar'
import errorutil from '../../Services/ErrorUtil'

const PAGE = "MENU::"

export const fetchMenu = dispatch => {
  API('/api/dish', '', '', 'GET')
    .then(res => {
      if(res.data.success){
        return dispatch({
          type: PAGE+"SUCCESS",
          data: res.data.data
        })
      }
      throw new Error("Unable to get dish")
    })
    .catch(err => {
      dispatch(snackbarError(errorutil(err)))
      console.log(err.response, err)
    })
  return ({
    type: PAGE+"REQUEST"
  })
}

export const deleteMenuItem = (dispatch, id) => {
  API('/api/dish/'+id, '', '', 'DELETE')
    .then(res => {
      if(res.data.success){
        dispatch(snackbarSuccess("Dish delete successfully"))
        return dispatch({
          type: PAGE+"DELETE_SUCCESS",
          id
        })
      }
      throw new Error("Unable to get dish")
    })
    .catch(err => {
      dispatch(snackbarError(errorutil(err)))
      console.log(err.response, err)
    })
  return ({
    type: PAGE+"DELETE_REQUEST"
  })
}

export const updateMenu = (data) => ({
  type: PAGE+"UPDATE",
  data
})