import { initialState } from './index'

const MenuReducer = (state = initialState.Menu, action) => {
  //console.log(action.type, action.type === "CATEGORY::SUCCESS")
  switch(action.type){
    case "MENU::REQUEST":
      return {
        ...state,
        isFetching: true
      }
    case "MENU::SUCCESS":
      return {
        ...state,
        isFetching: false,
        items: action.data
      }
    case "MENU::FAIL":
      return {
        ...state,
        isFetching: false
      }
    
    case "MENU::DELETE_REQUEST":
      return {
        ...state,
        deleteRequest: true
      }
    
    case "MENU::DELETE_SUCCESS":
      return {
        ...state,
        deleteRequest: false,
        items: state.items.filter(item => item.id !== action.id)
      }
    case "MENU::DELETE_FAIL":
      return {
        ...state,
        deleteRequest: false
      }
    
    case "MENU::UPDATE":
      return {
        ...state,
        items: state.items.map(item => item.id === action.data.id ? action.data : item)
      }
    
    default:
      return state;
  }
}

export default MenuReducer;