import React from 'react'
import Loading from '../../../Components/Loading'
import { getDish } from '../../../Store/actions/Dish'
import { fetchCategory } from '../../../Store/actions/Category'
import { connect } from 'react-redux'
import { useParams } from 'react-router-dom'
import { deserialize } from '../FormBuilder'
import { makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import CardMedia from '@material-ui/core/CardMedia'
import Divider from '@material-ui/core/Divider'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import { Head, Row } from '../../../Components/Table'

const styles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1)
  },
  media: {
    minHeight: 320,
  },
  content: {
    padding: theme.spacing(1)
  },
  heading: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    textTransform: 'uppercase',
    fontWeight: 400
  },
  description: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  },
  table: {
    position: 'relative',
    boxShadow: theme.shadows[1],
    borderRadius: 4,
    overflow: 'hidden',
    marginTop: theme.spacing(1)
  },
  tableHead: {
    background: '#212121',
    color: '#fff'
  }
}))

function ViewDishComponent(props){
  const classes = styles()
  console.log(props)
  return <div className={classes.root}>
    <Paper>
      <Loading isLoading={props.loading}>
        <Grid container>
          <Grid item xs={12} sm={6}>
          <CardMedia
            className={classes.media}
            image={process.env.REACT_APP_URL+props.image}
            title={props.label}
            img={props.image}
          />
          </Grid>
          <Grid item xs={12} sm={6} className={classes.content}>
            <Typography variant="h5" className={classes.heading}>{props.label}</Typography>
            <Divider />
            <Typography variant="h6">{props.category}</Typography>
            <Typography variant="body1" className={classes.description}>{props.description}</Typography>
            <Table className={classes.table}>
              <Head heading={['Sl No', 'Size', 'Price']} headerStyle={classes.tableHead}/>
              <TableBody>
                {
                  props.serving.map((serve, index) => <Row key={serve.id} row={[index+1, serve.size, serve.price]}/>)
                }
              </TableBody>
            </Table>
          </Grid>
        </Grid>
      </Loading>
    </Paper>
  </div>
}

function ViewDish(props){
  let {dishId} = useParams()
  React.useEffect(() => {
    props.getDish(dishId)
    if(props.Category.items.length === 0){
      props.fetchCategory()
    }
  }, [])
  let data = deserialize(props.Dish.item)
  let category = "Unknow"
  for(let i = 0; i < props.Category.items.length; i++){
    //console.log(+data.category_id, +props.Category.items[i].id)
    if(+data.category_id === +props.Category.items[i].id){
      category = props.Category.items[i].label;
      //console.log("Matched")
      break
    }
  }

  return <ViewDishComponent loading={props.Dish.getRequest} serving={[]} {...props.Dish.item} category={category}/>
}

const _mapStateToProps = state => ({
  Dish: state.Dish,
  Category: state.Category
})


const _mapDispatchToProps = dispatch => ({
  getDish: id => dispatch(getDish(dispatch, id)),
  fetchCategory: () => dispatch(fetchCategory(dispatch))
})

export default connect(_mapStateToProps, _mapDispatchToProps)(ViewDish);