import React from 'react'
import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
//import { Table, TableHead, TableCell, TableBody, TableRow } from '@material-ui/core'

export default function _Table(props) {
  return (
    <Table>
      <Head heading={props.heading} headerStyle={props.headerStyle}/>      
      <Body body={props.body} />
    </Table>
  )
}

export function Head(props) {
  return (
    <TableHead>
      <TableRow className={props.headerStyle}>
        {
          props.heading.map((item, index) => 
            <TableCell key={index} className={props.headerStyle}> {item} </TableCell>
          )
        }
      </TableRow>
    </TableHead>
  )
}

export function Body(props) {
  return (
    <TableBody>
      {
        props.body.map((row, index) => 
          <Row key={index} row={row} rowStyle={props.rowStyle}/>
        )
      }
    </TableBody>
  )
}

export function Row(props) {
  return (
    <TableRow className={props.rowStyle}>
      {
        props.row.map((column, index) => 
          <TableCell key={index}>{column}</TableCell>
        )
      }
    </TableRow>
  )
}