import React from 'react'
import useMediaQuery from '@material-ui/core/useMediaQuery'
import { makeStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import Divider from '@material-ui/core/Divider'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import { useHistory, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { logout } from '../../Store/actions/Auth'
import Category from '@material-ui/icons/Category'
import ExitToApp from '@material-ui/icons/ExitToApp'
import Person from '@material-ui/icons/Person'
import Restaurant from '@material-ui/icons/Restaurant'
import Search from '@material-ui/icons/Search'
import Hidden from '@material-ui/core/Hidden'
import AttachMoney from '@material-ui/icons/AttachMoney'
import BottomNavigation from '@material-ui/core/BottomNavigation'
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction'
import AuthService from '../../Services/AuthService'

const styles = makeStyles(theme => ({
  toolbar: theme.mixins.toolbar,
  bottomNav: {
    position: 'fixed',
    zIndex: 99,
    width: '100%',
    left: 0,
    bottom: 0
  }
}))


const Item = props => 
<ListItem onClick={props.onClick} button selected={props.selected}>
<ListItemIcon>{props.icon}</ListItemIcon>
  <ListItemText primary={props.title} />
</ListItem>


function SideNav(props) {
  const matches = useMediaQuery('(min-width:600px)')
  const classes = styles()
  let history = useHistory()
  let [activeIndex, setActiveIndex] = React.useState(0)

  //console.log(props);

  function itemClick(path, index) {
    history.push(path)
    props.handleDrawerToggle(false)
    setActiveIndex(index)
  }

  //console.log(props.open)
  return (
    <React.Fragment>
    <Drawer
      variant={matches ? "permanent": "temporary"}
      open={props.open}
      onClose={() => props.handleDrawerToggle(false)}
      classes={{
        paper: props.drawerStyle,
      }}
      ModalProps={{
        keepMounted: true, // Better open performance on mobile.
      }}
    >
      <div className={classes.toolbar} />
      <Divider />
      <List>
        <Item icon={<Restaurant />} title={'Menu'} onClick={() => itemClick("/dish/menu", 0)} selected={activeIndex === 0} />
        <Item icon={<Category/>} title={'Category'} onClick={() => itemClick("/category", 1)} selected={activeIndex === 1}/>
        <Item icon={<Search />} title={"Search"} onClick={() => itemClick("/search", 2)} selected={activeIndex === 2} />
        <Item icon={<AttachMoney />} title={"Payment Method"} onClick={() => itemClick("/payment_method", 3)} selected={activeIndex === 3}/>
      </List>
      <Divider />
      <List>
        {
          !AuthService.isLoggedIn() ?
              <Item icon={<Person />} title={'Login'} onClick={() => itemClick("/login", 10)} selected={activeIndex === 10}/>        
          :
          <React.Fragment>
            <Item icon={<Person />} title={AuthService.getUsername()} onClick={() => itemClick('/settings', 11)}/>
            <Item icon={<ExitToApp />} title={'Logout'} onClick={() => {itemClick("/login", 10); props.logout()}}/>
          </React.Fragment>
        }
      </List>
    </Drawer>
    <Hidden smUp>
      <BottomNavigation value={activeIndex} className={classes.bottomNav}>
        <BottomNavigationAction value={0} label="Menu" icon={<Restaurant />} onClick={() => itemClick('/dish/menu', 0)}/>
        <BottomNavigationAction value={1} label="Catgeory" icon={<Category />} onClick={() => itemClick('/category', 1)}/>
        <BottomNavigationAction value={2} label="Search" icon={<Search />} onClick={() => itemClick('/search', 2)}/>
        <BottomNavigationAction value={11} label="Settings" icon={<Person />} onClick={() => itemClick('/settings', 11)}/>
      </BottomNavigation>
    </Hidden>
    </React.Fragment>
  )
}

const mapDispatchToProps = dispatch => ({
  logout: () => dispatch(logout())
})

export default withRouter(connect(null, mapDispatchToProps)(SideNav));