import API from '../../Services/API'
import { snackbarError } from './snackbar'
import errorutil from '../../Services/ErrorUtil'

const PAGE = "CATEGORY::"

export const fetchCategory = dispatch => {

  API('/api/category', '', '', 'GET')
    .then(res => {
      if(res.data.success){
        //console.log(PAGE+"SUCCESS")
        dispatch({
          type: PAGE+"SUCCESS",
          data: res.data.data
        })
        return
      }
      //console.log(res.data)
      throw new Error("Unable to fetch category")
    })
    .catch(err => {
      console.log(err)
      dispatch(snackbarError(errorutil(err)))
      dispatch({
        type: PAGE+"FAIL"
      })
    })

  return ({
    type: PAGE+"REQUEST"
  })
}


export const addCategory = (dispatch, data) => {
  API('/api/category', data, '', 'POST')
    .then(res => {
      if(res.data.success){
        return dispatch({
          type: PAGE+"ADD_SUCCESS",
          data: res.data.data.item
        })
      }
      throw new Error(res.data.data.message)
    })
    .catch(err => {
      dispatch(snackbarError(errorutil(err)))
      dispatch({
        type: PAGE+"ADD_FAIL"
      })
    })
  return ({
    type: PAGE+"ADD_REQUEST"
  })
}

export const deleteCategory = (dispatch, id) => {
  API('/api/category/'+id, '', '', 'DELETE')
    .then(res => {
      if(res.data.success){
        return dispatch({
          type: PAGE+"DELETE_SUCCESS",
          id
        })
      }
      throw new Error(res.data.data.message)
    })
    .catch(err => {
      dispatch(snackbarError(errorutil(err)))
      dispatch({
        type: PAGE+"DELETE_FAIL"
      })
    })
  return ({
    type: PAGE+"DELETE_REQUEST"
  })
}