import React from 'react'
import List from '@material-ui/core/List'
import Loading from '../../Components/Loading'
import LoadingButton from '../../Components/LoadingButton'
import Paper from '@material-ui/core/Paper'
import { makeStyles } from '@material-ui/core/styles'
import { connect } from 'react-redux'
import { fetchCategory, addCategory, deleteCategory } from '../../Store/actions/Category'
import ListSubheader from '@material-ui/core/ListSubheader'
import Delete from '@material-ui/icons/Delete'
import TextField from '@material-ui/core/TextField'
import ItemRow from '../../Components/ItemRow'

const styles = makeStyles(theme => ({
  container: {
    padding: theme.spacing(1)
  },
  paper: {
    padding: theme.spacing(1)
  },
  addCategory: {
    display: 'flex'
  },
  textField: {
    flex: 1
  }
}))

export function Add(props) {
  let [label, setLabel] = React.useState('')

  function addRequest(){
    if(label.length > 1)
      props.addRequest(label)
    console.log(label)
  }

  return (
    <div className={props.classes.addCategory}>
      <TextField label={props.label} className={props.classes.textField} value={label} onChange={e => setLabel(e.target.value)}/>
      <LoadingButton isLoading={props.request} variant="contained" color="primary" onClick={addRequest}>Add</LoadingButton>
    </div>
  )
}

export function CategoryComponent({items, ...props}) {
  const classes = styles()
  return (
    <div className={classes.container}>
      <Paper className={classes.paper}>
        <Add
          label={"Add "+props.heading}
          classes={classes} 
          addRequest={props.addItem} 
          request={props.addRequest}/>
        <List subheader={<ListSubheader>{props.heading}</ListSubheader>}>
          <Loading isLoading={props.itemsRequest}>
            {
              items.map(item => <ItemRow
                key={item.id}
                actionClick={() => props.actionClick(item.id)}
                text={item.label}
                secondaryIcon={<Delete />}
              />)
            }
          </Loading>
        </List>
      </Paper>
    </div>
  )
}

function Category(props) {

  React.useEffect(() => {
    if(props.Category.items.length === 0)
      props.fetchCategory()
  }, [])

  const deleteCategory = id => {
    if(window.confirm("Do you wish to remove?"))
      props.deleteCategory(id)
  }

  const addItem = label => {
    props.addCategory({label})
  }


  return (
    <CategoryComponent 
      items={props.Category.items}
      addItem={addItem}
      addRequest={props.Category.addRequest}
      itemsRequest={props.Category.isFetching}
      heading={'Category'}
      actionClick={deleteCategory}
      />
  )

}


const mapStateToProps = state => ({
  Category: state.Category
})

const mapDispatchToProps = dispatch => ({
  fetchCategory: () => dispatch(fetchCategory(dispatch)),
  addCategory: label => dispatch(addCategory(dispatch, label)),
  deleteCategory: id => dispatch(deleteCategory(dispatch, id))
})

export default connect(mapStateToProps, mapDispatchToProps)(Category)