import React from 'react'
import Refresh from '@material-ui/icons/Refresh'
import Settings from '@material-ui/icons/Settings'
import List from '../../Components/List'
import IconButton from '@material-ui/core/IconButton'


function RefreshComponent({props}){
  let _ = [{
    primaryText: "Refresh Menu",
    triggerText: "REFRESH_MENU"
  }]
  function makeItems(items){
    return items.map((item, index) => ({
      id: index,
      icon: <Settings />,
      secondaryIcon: <IconButton onClick={() => console.log("Refresh was clicked")}><Refresh /></IconButton>,
      ...item
    }))
  }
  let items = makeItems(_)
  return (
    <List
      heading={"Refresh items"}
      items={items}
    />
  )
}

export default RefreshComponent