import React from 'react'
import Paper from '@material-ui/core/Paper'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import { Redirect } from 'react-router-dom'
import TextField from '@material-ui/core/TextField'
import Divider from '@material-ui/core/Divider'
import { connect } from 'react-redux'
import LoadingButton from '../../Components/LoadingButton'
import {login} from '../../Store/actions/Auth'

const styles = makeStyles(theme => ({
  container: {
    padding: theme.spacing(1)
  },
  paper: {
    padding: theme.spacing(1),
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

function Login(props) {
  let [username, setUsername] = React.useState('')
  let [password, setPassword] = React.useState('')

  const classes = styles();

  function initLogin(){
    props.login({username, password})
  }
  
  if(props.Auth.token !== ""){
    console.log("COnsole.log now must be redirected", process.env.REACT_APP_PATH)
    return <Redirect to={process.env.REACT_APP_PATH} />
  }

  return (
    <div className={classes.container}>
      <Paper className={classes.paper}>
        <Typography>LOGIN: </Typography>
        <Divider />
        <TextField 
          fullWidth 
          value={username}
          onChange={e => setUsername(e.target.value)}
          label={'Username'} />
        <TextField 
          fullWidth 
          type="password"
          value={password}
          onChange={e => setPassword(e.target.value)}
          label={'Password'} />
        <LoadingButton 
          type="submit" 
          variant="contained" 
          color="primary" 
          onClick={initLogin}
          fullWidth 
          isLoading={props.Auth.isRequesting}>Login</LoadingButton>
      </Paper>
    </div>
  )
}

const mapStateToProps = state => ({
  Auth: state.Auth
})

const mapDispatchToProps = dispatch => ({
  login: (data) => dispatch(login(dispatch, data))
})

export default connect(mapStateToProps, mapDispatchToProps)(Login);