import React from 'react'
import FormControl from '@material-ui/core/FormControl'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  formRow: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    paddingBottom: theme.spacing(1),
    alignItems: 'stretch'
  },
  label: {
    paddingLeft: theme.spacing(1),
    border: '1px solid #efefef',
    flex: 1,
    alignItems:'center',
    display: 'flex'
  }
}))

function UploadComponent(props){
  const classes = styles()
  return(
    <FormControl fullWidth className={classes.formRow+" "+classes.container}>
      <label className={classes.label} htmlFor="#file_upload">{props.file.name || "No file selected"}</label>
      <Button onClick={props.onButtonClick} variant="contained" color="secondary" className={classes.half}> Select File </Button>
      <input type="file" name="file_upload"  id={'file_upload'} hidden onChange={props.handleChange}/>
    </FormControl>
  )
}

function Upload(props){

  let [file, setFile] = React.useState('')

  function buttonClick(){
    document.querySelector("#file_upload").click()
  }

  function handleChange(e){
    if(e.target.files.length === 0){
      return 
    }
    setFile(e.target.files[0])
  }
  //console.log(file)

  return (
    <UploadComponent 
      onButtonClick={buttonClick}
      handleChange={handleChange}
      file={file}
      />
  )
}

export default Upload