import React from 'react'
import List from '../../Components/List'
import { makeStyles } from '@material-ui/core/styles'
import Switch from '@material-ui/core/Switch'
import Menu from '@material-ui/icons/Menu'
import Category from '@material-ui/icons/Category'
import AttachMoney from '@material-ui/icons/AttachMoney'
import { connect } from 'react-redux'
import RefreshComponent from './RefreshComponent'
import { toggleRefreshCategory, toggleRefreshMenu, toggleRefreshPayment, constants, settings } from '../../Store/actions/Settings'

const styles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1),
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

export function SettingsComponent({settings, ...props}){
  const classes = styles()
  let items = [
    {
      id: 1,
      primaryText: "Auto Refresh Menu",
      icon: <Menu />,
      secondaryIcon: <Switch checked={settings.autoRefreshMenu} onClick={() => props.settingsDispatch(constants.toggleRefreshMenu)}/>
    },
    {
      id: 2,
      primaryText: "Auto refresh Category",
      icon: <Category />,
      secondaryIcon: <Switch checked={settings.autoRefreshCatgeory} onClick={() => props.settingsDispatch(constants.toggleRefreshCategory)}/>
    },
    {
      id: 3,
      primaryText: "Auto refresh Payment",
      icon: <AttachMoney />,
      secondaryIcon: <Switch checked={settings.autoRefreshPayment} onClick={() => props.settingsDispatch(constants.toggleRefreshPayment)}/>
    }
  ]
  return (
  <div className={classes.root}>
    <List 
      heading={'Site settings'}
      items={items}
      divider={true}
      />
    <RefreshComponent />
  </div>
  )
}

function Settings({settings, ...props}){
  const classes = styles()

  return <div className={classes.root}>
    <SettingsComponent 
      settings={settings}
      settingsDispatch={type => props.settingsDispatch(type)}
    />
  </div>
}

const mapStateToProps = state => ({
  settings: state.Settings
})

const mapDispatchToProps = dispatch => ({
  settingsDispatch: (type) => dispatch(settings(type))
})

export default connect(mapStateToProps, mapDispatchToProps)(Settings)