import { initialState } from './index'

const DishReducer = (state = initialState.Dish, action) => {
  //console.log(action.type, action.type === "CATEGORY::SUCCESS")
  switch(action.type){
    case "DISH::ADD_REQUEST":
      return {
        ...state,
        addRequest: true
      }
    case "DISH::ADD_SUCCESS":
      return {
        ...state,
        addRequest: false
      }
    case "DISH::ADD_FAIL":
      return {
        ...state,
        addRequest: false
      }
    
    case "DISH::GET_REQUEST":
      return {
        ...state,
        getRequest: true,
        item: {}
      }
    
    case "DISH::GET_SUCCESS":
      return {
        ...state,
        getRequest: false,
        item: action.data
      }
    case "DISH::GET_FAIL":
      return {
        ...state,
        getRequest: false,
      }
    
    case "DISH::UPDATE_SUCCESS":
      return {
        ...state,
        updateRequest: false,
        error: false
      }
    case "DISH::UPDATE_REQUEST":
      return {
        ...state,
        updateRequest: true,
      }
    
    default:
      return state

  }
}

export default DishReducer