import React from 'react'
import Grid from '@material-ui/core/Grid'
import FormControl from '@material-ui/core/FormControl'
import TextField from '@material-ui/core/TextField'
import LoadingButton from '../../../Components/LoadingButton'
import { makeStyles } from '@material-ui/core/styles'
import Divider from '@material-ui/core/Divider'
import Select from '@material-ui/core/Select'
import MenuItem  from '@material-ui/core/MenuItem'
import { fetchCategory } from '../../../Store/actions/Category'
import { connect } from 'react-redux'
import Upload from './Upload'
import formBuilder from '../FormBuilder'
import { addDish } from '../../../Store/actions/Dish'

const useStyles = makeStyles(theme => ({
  container: {
    background: "#fff",
    margin: theme.spacing(1),
    width: `calc(100% -  ${theme.spacing(2)}px)`,
    boxShadow: theme.shadows[1],
    borderRadius: 4,
    position: 'relative',
    padding: theme.spacing(1)
  },
  heading: {
    margin: 0,
    paddingBottom: theme.spacing(1),
    fontWeight: 400,
  },
  formRow: {
    paddingBottom: theme.spacing(1)
  }
}))

const FormRow =({ rowClass, ...props}) => {
  let [value, setValue] = React.useState(props.value)
  React.useEffect(() => {
    setValue(props.value)
  }, [props.value])

  return <FormControl fullWidth className={rowClass}>
    <TextField fullWidth {...props} value={value} onChange={e => setValue(e.target.value)}/>
  </FormControl>
}

const sizes = [
  {
    label: "Regular Price",
    name: "r_price"
  },
  {
    label: "Medium Price",
    name: "m_price"
  },
  {
    label: "Large Price",
    name: "l_price"
  }
]

function _SelectComponent(props) {
  let [selected, setSelected] = React.useState(props.selected || -1)
  //console.log("This is selected", selected, props.selected)
  if(props.items.length === 0){
    props.fetchCategory()
  }
  React.useEffect(() => {
    setSelected(props.selected)
  }, [props.selected])

  return (
    <FormControl className={props.classes.formRow} fullWidth>
      <Select value={selected} onChange={e => setSelected(e.target.value)} fullWidth name="category_id">
        <MenuItem value={-1}>Select serving</MenuItem>
        {
          props.items.map(option => 
            <MenuItem key={option.id} value={option.id}>{option.label}</MenuItem>
          )
        }
      </Select>
    </FormControl>
  )
}

const mapStateToProps = state => ({
  items: state.Category.items
})

const mapDispatchToProps = dispatch => ({
  fetchCategory: () => dispatch(fetchCategory(dispatch))
})

const SelectComponent = connect(mapStateToProps, mapDispatchToProps)(_SelectComponent)

export function AddDishComponent(props){
  //console.log("Add component props", props)
  const classes = useStyles()
  return (
    <form component={'form'}  onSubmit={props.onSubmit} className={classes.container}>
      <h1 className={classes.heading}>Dish details</h1>
      <Divider />
      <FormRow label={'Title'} rowClass={classes.formRow} name="label" required value={props.label || ''}/>
      <FormRow label={'Description'} rows={5} multiline={true} rowClass={classes.formRow} name="description" required value={props.description || ''}/>
      <Grid container justify={"center"} spacing={1}>
        {sizes.map(item => <Grid item xs={6} key={item.label}>
          <FormRow label={item.label} type="number" rowClass={classes.formRow} name={item.name} value={props[item.name] || 0}/>
        </Grid>)}
      </Grid>
      <SelectComponent classes={classes} selected={props.category_id || -1}/>
      <Upload />
      <Divider />
      <LoadingButton type="submit" isLoading={props.loading} variant="contained" fullWidth color="primary">Add Dish</LoadingButton>
    </form>
  )
}

function AddDish(props){

  function formSubmitted(e){
    console.log("Submit request")
    e.preventDefault()
    let el = e.target
    let inputs = el.querySelectorAll("[name]")
    let data = {}
    for(let i = 0; i < inputs.length; i++){
      data[inputs[i].getAttribute("name")] = inputs[i].value
    }

    let file = document.querySelector("[type=file]").files;
    console.log(file)
    data.file_upload = file[0]
    //console.log(data)
    if(props.updateDish){
      props.updateDish(formBuilder(data))
    }else{
      props.addDish(formBuilder(data))
    }
  }

  return (
    <AddDishComponent 
      onSubmit={formSubmitted} 
      loading={props.Dish.addRequest}
      {...props}/>
  )

}

const _mapStateToProps = state => ({
  Dish: state.Dish
})


const _mapDispatchToProps = dispatch => ({
  addDish: data => dispatch(addDish(dispatch, data))
})


export default connect(_mapStateToProps, _mapDispatchToProps)(AddDish);