export default function formBuilder(formData){

  const data = new FormData()
  data.append('label', formData.label)
  data.append('description', formData.description)
  data.append('category_id', formData.category_id)
  if(formData.file_upload !== undefined && formData.file_upload !== "" && typeof formData.file_upload !== 'string')
    data.append('image', formData.file_upload, formData.file_upload.name)

  let serving = []
  if(formData.r_price !== undefined){
    serving.push({
      size: "R",
      price: formData.r_price
    })
  }

  if(formData.m_price !== undefined){
    serving.push({
      size: "M",
      price: formData.m_price
    })
  }
  if(formData.l_price !== undefined){
    serving.push({
      size: "L",
      price: formData.l_price
    })
  }
  //data.serving = serving
  //console.log(formData)
  data.append('serving', JSON.stringify(serving))
  return data

}

export const deserialize = formData => {
  //console.log(formData)
  if(formData === undefined || formData === {}){
    return {}
  }
  let data = {
    label: formData.label,
    description: formData.description,
    category_id: formData.category_id,
    image: formData.image
  }
  let obj = {}
  const arr = formData.serving
  if(arr === undefined){
    return {}
  }
  //console.log(arr)
  for(let i = 0; i < arr.length; i++) {
    if(arr[i].size === "R")
      obj = {
        ...obj, 
        r_price: arr[i].price
      }
    else if(arr[i].size === "M")
          obj = {
            ...obj, 
            m_price: arr[i].price
          }
        else
          obj = {
            ...obj, 
            l_price: arr[i].price
          }
      }
  data = {
    ...data,
    ...obj
  }
  //console.log(data)
  return data
}