import { combineReducers } from 'redux'
import Snackbar from './snackbar'
import Auth from './Auth'
import Category from './Category'
import Dish from './Dish'
import Menu from './Menu'
import Settings from './Settings'

export const initialState = {
  Snackbar: {
    open: false,
    message: 'This is default message',
    type: 'success',
    duration: 6000
  },
  Auth: {
    isRequesting: false,
    token: '',
    username: '',
    error: false,
  },
  Category: {
    isFetching: false,
    items: [],
    addRequest: false,
    deleteRequest: false
  },
  Dish: {
    addRequest: false,
    error: false,
    getRequest: false
  },
  Menu: {
    isFetching: false,
    items: []
  },
  Settings: {
    autoRefreshMenu: false,
    autoRefreshCategory: false,
    autoRefreshPayment: true
  }
}

const reducers = combineReducers({
  Snackbar,
  Auth,
  Category,
  Dish,
  Menu,
  Settings
})

export default reducers;